.page-header img{
  max-width: 1200px;
  margin: auto;
  height: auto;
  display: block;
}

.desktop-header-img{
    width: 50%;
}

.mobile-header-img{
    width: 100%;
}

.page-head-container{
  background-color: #0033A1 !important;
}

:local(.l-grid__col){
  padding: 0px !important;
  margin: 0px !important;
}

:local(.l-grid){
  margin-right: 0px !important;
}

.row{
  margin: 0px !important;
}

.pad-nav{
  background-color: #fcfcfc;
  border-bottom: .062rem solid #dcdcdc;
  border-top: .062rem solid #dcdcdc;
}

.c-sub-nav{
  height: 45px !important;
  border-bottom: 1px !important;
  border-top: 1px !important;
}

.nav-gutter{
  width: 100%; /*change back to 100 */
  height: 47px;

  background-color: #fcfcfc;
  border-bottom: .062rem solid #dcdcdc;
  border-top: .062rem solid #dcdcdc;
}

.head-gutter{
  background-color: #0033A1;
}

.body-content{
    margin-top: 3rem;
}

