:local(.container){
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
}

:local(.card){
  color: #4E5968;
  margin-bottom: 3rem;
  margin-top: 3rem;
  background-color: #F4F6F9;
  padding: 20px;
  -webkit-box-shadow: 5px 5px 15px rgba(0,0,0,0.3);
          box-shadow: 5px 5px 15px rgba(0,0,0,0.3);
}

:local(.iconContainer){
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

:local(.icon){
  display: block;
  margin: auto;
  margin-top: 15px;
  width: 50%;
  height: 60px;
  fill: #4E5968;
}



